import React from "react";
import {Box} from "@material-ui/core";
import {CartesianGrid, LabelList, Legend, Line, LineChart, XAxis, YAxis} from "recharts";

interface ZgonyWTygodniu {
    podregion: string;
    tydzien: string;
    2014: number;
    2015: number;
    2016: number;
    2017: number;
    2018: number;
    2019: number;
    2020: number;
}

export interface ZgonyProps {
    podregion: string;
    zgony: ZgonyWTygodniu[];
}

export class Zgony extends React.Component<ZgonyProps> {
    render() {
        const {podregion, zgony} = this.props;
        console.log(zgony);
        return <Box><Box>Podregion {podregion}: wszystkie zgony wg. tygodnia:</Box>
            <LineChart syncId={"zgony"} width={530} height={250} data={zgony}
                   margin={{ top: 5, right: 15, left: 10, bottom: 5 }}>
            <CartesianGrid strokeDasharray="5 5" />
            <XAxis dataKey="tydzien" />
            <YAxis />
            <Legend />
            <Line type="linear" dot={false} dataKey="2014" stroke="#92daad">
            </Line>
            <Line type="linear" dot={false} dataKey="2015" stroke="#82ca9d">
            </Line>
            <Line type="linear" dot={false} dataKey="2016" stroke="#72ba8d">
            </Line>
            <Line type="linear" dot={false} dataKey="2017" stroke="#62aa7d">
            </Line>
            <Line type="linear" dot={false} dataKey="2018" stroke="#529a6d">
            </Line>
            <Line type="linear" dot={false} dataKey="2019" stroke="#428a5d">
            </Line>
            <Line type="linear" dot={false} dataKey="2020" stroke="#ff2222">
            </Line>
        </LineChart>
        </Box>;
    }
}