import React from "react";
// @ts-ignore
import DropdownMultiselect from "react-multiselect-dropdown-bootstrap";

interface WyborModulowProps {
    zmianaModuluFn: (moduly: string[]) => void;
    moduly: string[];
}

export class WyborModulow extends React.Component<WyborModulowProps> {
    render() {
        const {moduly, zmianaModuluFn} = this.props;
        return <DropdownMultiselect options={moduly} selected={moduly} handleOnChange={(selected: string[]) => zmianaModuluFn(selected)}>
        </DropdownMultiselect>
    }
}