import React from "react";
import {Box} from "@material-ui/core";
import {CartesianGrid, LabelList, Legend, Line, LineChart, XAxis, YAxis} from "recharts";

export interface Zatrudnienie {
    rok: number;
    przedszkola?: number,
    podstawowe?: number,
    zawodowe?: number,
    gimnazja?: number,
    licea?: number,
}

interface NauczycieleProps {
    dane: Zatrudnienie[];
}

export class Nauczyciele extends React.Component<NauczycieleProps> {
    render() {
        const {dane} = this.props;
        console.log(dane);
        return <Box><Box>Etaty nauczycielskie wg rodzaju szkoły:</Box>
            <LineChart syncId={"nauczyciele"} width={530} height={250} data={dane}
                   margin={{ top: 5, right: 15, left: 10, bottom: 5 }}>
            <CartesianGrid strokeDasharray="5 5" />
            <XAxis dataKey="rok" />
            <YAxis />
            {/*<Tooltip />*/}
            <Legend />
            <Line type="monotone" dataKey="przedszkola" stroke="#0884d8">
                {/*<LabelList dataKey="podstawowe" position="bottom" />*/}
            </Line>
            <Line type="monotone" dataKey="podstawowe" stroke="#8884d8">
                {/*<LabelList dataKey="podstawowe" position="bottom" />*/}
            </Line>
            <Line type="monotone" dataKey="gimnazja" stroke="#0884d8">
                {/*<LabelList dataKey="gimnazja" position="top" />*/}
            </Line>
            <Line type="monotone" dataKey="zawodowe" stroke="#080408">
                {/*<LabelList dataKey="zawodowe" position="top" />*/}
            </Line>
            <Line type="monotone" dataKey="licea" stroke="#82ca9d">
                {/*<LabelList dataKey="licea" position="top" />*/}
            </Line>
        </LineChart>
        </Box>;
    }
}