import React, {FormEvent} from 'react';
import './App.css';
import {exportComponentAsPNG} from 'react-component-export-image';
import Box from "@material-ui/core/Box";
import {PowiatEls, SelektorGminy} from "./SelektorGminy";
import {Dane, Panel} from "./Panel";
import axios from "axios";
import {SelektorWojewodztwa} from "./SelektorWojewodztwa";
import {WyborModulow} from "./WyborModulow";
import { ZgonyProps } from './Zgony';
import {Zatrudnienie} from "./Nauczyciele";
import {Uczniowie} from "./Uczniowie";

interface AppProps {
}

interface AppState {
    cytat: string;
    autor: string;
    select: string[];
    liczbaDzialow: string;
    dane?: Dane;
    powiaty?: Map<string, PowiatEls>;
    zgony: ZgonyProps;
    nauczyciele: Zatrudnienie[];
    uczniowie: Uczniowie[];
}

class App extends React.Component<AppProps, AppState> {
    classes : any;
    componentRef : any;

    wojewodztwa = [
        "DOLNOŚLĄSKIE",
        "KUJAWSKO-POMORSKIE",
        "LUBELSKIE",
        "LUBUSKIE",
        "ŁÓDZKIE",
        "MAŁOPOLSKIE",
        "MAZOWIECKIE",
        "OPOLSKIE",
        "PODKARPACKIE",
        "PODLASKIE",
        "POMORSKIE",
        "ŚLĄSKIE",
        "ŚWIĘTOKRZYSKIE",
        "WARMIŃSKO-MAZURSKIE",
        "WIELKOPOLSKIE",
        "ZACHODNIOPOMORSKIE",
    ]

    MODULY = [
        "budzet",
        "smieci",
        "dotacje_ue",
        "dzialy",
        "nauczyciele",
        "uczniowie",
        "zgony",
    ]

    constructor(props: AppProps) {
        super(props);
        this.componentRef = React.createRef();

        this.handleChangeAutor = this.handleChangeAutor.bind(this);
        this.handleChangeCytat = this.handleChangeCytat.bind(this);
        this.handleChangeMultiple = this.handleChangeMultiple.bind(this);

        this.pobierzDaneGminy = this.pobierzDaneGminy.bind(this);
        this.pobierzListePowiatow = this.pobierzListePowiatow.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

        this.state = {
            cytat: "np: Kraków w 2019 realizował dobrą politykę budżetową. Wprawdzie wydatki przewyższyły dochody, ale środki te zostały wydane na wkład własny w projekty inwestycyjne dofinansowane przez UE, które będą procentować w przyszłych latach.",
            autor: "np.: Maciej Pikor, Lider Polska 2050 w Małopolsce",
            select: this.MODULY,
            liczbaDzialow: "10",
            zgony: {podregion: '', zgony: []},
            nauczyciele: [],
            uczniowie: [],
        };
    }

    handleChangeAutor(autor: string) {
        this.setState(Object.assign({}, this.state, {autor: autor}));
    }

    handleChangeCytat(cytat: string) {
        this.setState(Object.assign({}, this.state, {cytat: cytat}));
    }

    handleChangeMultiple(values: string[]) {
        this.setState(Object.assign({}, this.state, {select: values}));
    }

    handleChangeLiczbaDzialow(liczbaDzialow: string) {
        this.setState(Object.assign({}, this.state, {liczbaDzialow: liczbaDzialow}));
    }

    handleSubmit(event: FormEvent) {
        event.preventDefault();
    }

    //TODO: use redux
    render() {
        const {cytat, autor, select, liczbaDzialow, dane, powiaty, zgony, nauczyciele, uczniowie} = this.state;
        const wojewodztwa = this.wojewodztwa;
        return (

            // https://www.npmjs.com/package/react-component-export-image
            <Box display="flex" flexDirection="row">
                <Panel dane={dane} ref={this.componentRef} cytat={cytat} autor={autor} selected={select}
                       liczbaDzialow={liczbaDzialow} zgony={zgony} nauczyciele={nauczyciele} uczniowie={uczniowie}/>
                <form onSubmit={this.handleSubmit}>
                    <Box display="flex" flexDirection="column" style={{padding: 10}}>
                        <strong>Wybór Jednostki Samorządu:</strong>
                        <SelektorWojewodztwa wojewodztwa={wojewodztwa} wojewodztwoChangeFn={this.pobierzListePowiatow}/>
                        <SelektorGminy powiaty={powiaty} gminaChangeFn={this.pobierzDaneGminy}/>
                        <br/>
                        <strong>Komentarz eksperta:</strong>
                            <textarea name="cytat" onChange={ev=>this.handleChangeCytat(ev.target.value)} value={cytat}
                                      style={{marginBottom: 10}}/>
                            <input type="text" name="autor" onChange={ev=>this.handleChangeAutor(ev.target.value)}
                                   value={autor}/>
                            <br/>
                            <strong>Elementy grafiki:</strong>
                            <WyborModulow moduly={this.MODULY} zmianaModuluFn={this.handleChangeMultiple}/>
                        <strong>Max. liczba działów na wykresie:</strong>
                        <input type="text" name="liczbaDzialow" value={liczbaDzialow} style={{marginBottom: 10}}
                               onChange={ev=>this.handleChangeLiczbaDzialow(ev.target.value)}/>
                        <button onClick={() => exportComponentAsPNG(this.componentRef)}>
                            Export PNG
                        </button>
                    </Box>
                </form>
            </Box>
        );
    }

    pobierzDaneGminy(kodGminy: string) {
        const apiUrl = '/files/' + kodGminy + '-panel.json';
        axios.get(apiUrl).then((response) => {
            const ret: Dane = response.data;
            console.log(ret);
            this.setState(Object.assign({}, this.state, {dane: ret}));
            if (ret.podregion_eurostat) {
                this.pobierzZgony(ret.podregion_eurostat)
            }
            if (ret.kod_gus) {
                this.pobierzNauczycieli(ret.kod_gus);
                this.pobierzUczniowie(ret.kod_gus);
            }
        });
    }

    pobierzListePowiatow(wojewodztwo: string) {
        let wojName = wojewodztwo.replaceAll("Ś", "S").replaceAll("Ą", "A").replaceAll("Ń", "N").replaceAll("Ł", "L");
        const apiUrl = '/files/' + wojName + '.json';
        axios.get(apiUrl).then((response) => {
            const ret = new Map<string, PowiatEls>();
            for (const x in response.data) {
                if (response.data.hasOwnProperty(x)) {
                    const gminy = new Map<string, string>();
                    for (const y in response.data[x]) {
                        if (response.data[x].hasOwnProperty(y)) {
                            gminy.set(y, response.data[x][y]);
                        }
                    }
                    const p: PowiatEls = {gminy: gminy};
                    ret.set(x, p);
                }
            }
            this.setState(Object.assign({}, this.state, {powiaty: ret}));
            const pierwszaGmina = ret.values().next().value.gminy.values().next().value;
            this.pobierzDaneGminy(pierwszaGmina);
        });
    }

    pobierzZgony(podregion: string) {
        const apiUrl = '/files/' + podregion + '.json';
        axios.get(apiUrl).then((response) => {
            let x = response.data
            this.setState(Object.assign({}, this.state, {zgony: x}));
        });
    }

    pobierzNauczycieli(kodGus: string) {
        let szkoly = {
            288152: "przedszkola",
            288154: "przedszkola",  // oddziały przy SP
            288155: "przedszkola",  // zespoły
            288156: "przedszkola",  // punkty
            377896: "podstawowe",   // bez specjalnych
            377897: "podstawowe",   // specjalne
            377895: "podstawowe",   // dla dorosłych
            378306: "zawodowe",
            378248: "gimnazja",
            378626: "licea",
            378627: "licea",        // specjalne
            378628: "licea",        // dla dorosłych
            378629: "licea",        // uzupełniające
            378630: "licea",        // uzupełniające specjalne
            378631: "licea",        // uzupełniające dla dorosłych
        }
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        for (let id in szkoly) {
            apiUrl += "&var-id=" + id;
        }
        let ret: Zatrudnienie[] = [{rok: 2015}, {rok: 2016}, {rok: 2017}, {rok: 2018}, {rok: 2019}];
        axios.get(apiUrl).then((response) => {
            for (let r of response.data.results) {
                for (let v of r.values) {
                    for (let z of ret) {
                        if (z.rok === Number.parseInt(v.year)) {
                            // @ts-ignore
                            if (z[szkoly[r.id]] !== undefined) {
                                // @ts-ignore
                                z[szkoly[r.id]] += v.val;
                            } else {
                                // @ts-ignore
                                z[szkoly[r.id]] = v.val;
                            }
                        }
                    }
                }
            }
            this.setState(Object.assign({}, this.state, {nauczyciele: ret}));
        });
    }

    pobierzUczniowie(kodGus: string) {
        let szkoly = {
            377227: "zlobki",
            151864: "przedszkola",
            151867: "podstawowe",   // bez specjalnych
            46733:  "podstawowe",   // oddziały specjalne przy SP
            46726:  "podstawowe",   // SP dla niepełnosprawnych
            455415: "zawodowe",
            2394:   "gimnazja",
            455392: "licea",        // razem
        }
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        for (let id in szkoly) {
            apiUrl += "&var-id=" + id;
        }
        let ret: Uczniowie[] = [{rok: 2015}, {rok: 2016}, {rok: 2017}, {rok: 2018}, {rok: 2019}];
        axios.get(apiUrl).then((response) => {
            for (let r of response.data.results) {
                for (let v of r.values) {
                    for (let z of ret) {
                        if (z.rok === Number.parseInt(v.year)) {
                            // @ts-ignore
                            if (z[szkoly[r.id]] !== undefined) {
                                // @ts-ignore
                                z[szkoly[r.id]] += v.val;
                            } else {
                                // @ts-ignore
                                z[szkoly[r.id]] = v.val;
                            }
                        }
                    }
                }
            }
            this.setState(Object.assign({}, this.state, {uczniowie: ret}));
        });
    }

    componentDidMount() {
        this.pobierzListePowiatow("DOLNOŚLĄSKIE");
    }
}

export default App;
